<script>
import Map from "@/views/components/Map"
import Food from "@/views/components/Food"
import Parking from "@/views/components/Parking"

async function copyText(text) {
  try {
    await navigator.clipboard.writeText(text)
  } catch (err) {
    copyText2()
  }
}

function copyText2() {
  let copyText2 = document.querySelector('#copyText')
  copyText2.setAttribute('type', 'text')
  copyText2.select()

  try {
    var successful = document.execCommand('copy')
    var msg = successful ? 'successful' : 'unsuccessful'
    console.log("copy was " + msg)
  } catch (err) {
    console.log("copy was " + err)
  }

  /* unselect the range */
  copyText2.setAttribute('type', 'hidden')
  window.getSelection().removeAllRanges()
}

export default {
  name: 'Info',
  created() {

  },
  mounted() {
    setTimeout(() => {this.initMap()}, 500)
  },
  beforeDestroy() {

  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  components: {
    Map,
    Food,
    Parking
  },
  methods: {
    initMap() {
      if (!this.isLoaded) {
        this.$refs.Map.initNaverMap()
        this.isLoaded = true
      }
    },
    openTmapApp() {
      console.log("openTmapApp")
      window.location.href = "https://apis.openapi.sk.com/tmap/app/routes?appKey=l7xxf3df8e921a6e43beb8fbcb43854b1c20&name=라온제나 강남&lon=127.038589254902&lat=37.5169632798641"
    },
    openNaverMapApp() {
      console.log("openNaverMapApp")
      if (this.browserType == "android") {
        window.location.href = "intent://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish#Intent;scheme=nmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.nhn.android.nmap;end"
      } else if (this.browserType == "iphone") {
        var clickedAt = +new Date()
        window.location.href = "nmap://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish"

        setTimeout(function() {
          if (+new Date() - clickedAt < 2200) {
            location.href = 'http://itunes.apple.com/app/id311867728?mt=8'
          }
        }, 2000)
      } else {
        window.location.href = "https://naver.me/FXZrWN73"
      }
    },
    openKakaoMapApp() {
      console.log("openKakaoMapApp")
      if (this.browserType == "android") {
        window.location.href = "kakaomap://search?q=라온제나 강남&p=37.5169632798641,127.038589254902"
      } else if (this.browserType == "iphone") {
        var clickedAt = +new Date()
        window.location.href = "kakaomap://search?q=라온제나 강남&p=37.5169632798641,127.038589254902"

        setTimeout(function() {
          if (+new Date() - clickedAt < 2200) {
            window.location.href = "https://map.kakao.com/link/to/라온제나 강남,37.5169632798641,127.038589254902"
          }
        }, 2000)
      } else {
        window.location.href = "https://map.kakao.com/link/to/라온제나 강남,37.5169632798641,127.038589254902"
      }
    },
    sendURLtoSMS() {
      this.copyClipboard("https://ldc0728.github.io/invitation-publish/")

      setTimeout(() => {
        if (this.browserType == "android") {
          window.location.href = "sms:?body=https://ldc0728.github.io/invitation-publish/"
        } else if (this.browserType == "iphone") {
          window.location.href = "sms:&body=https://ldc0728.github.io/invitation-publish/"
        }
      }, 1500)
    },
    copyClipboard(text) {
      this.copyText = text

      copyText(text)
      this.showAlert('청첩장 공유 URL을 복사했습니다!')
      setTimeout(() => {
        this.copyText = ""
      }, 500)
    },
    showAlert(alertText) {
      this.alertText = alertText
      this.alertBar = true
    },
  },
  data: () => ({
    isLoaded: false,
    tab: null,
    copyText: "",
    alertText: "",
    alertBar: false,
    alertTimeout: 1000,
  }),
}
</script>

