            <p class="p-subtitle">{{bankData.father}} ·<v-btn class="pa-0 ml-9" plain @click="copyClipboard('father')"><v-icon dense small class="mr-1">mdi-content-copy</v-icon>복사하기</v-btn><br/> {{accountNumberData.father}}</p>
            <v-divider> </v-divider>
