<script>
async function copyAccountNumber(text) {
  try {
    await navigator.clipboard.writeText(text)
  } catch (err) {
    copyAccountNumber2()
  }
}

function copyAccountNumber2() {
  let copyAccountNumber = document.querySelector('#copyAccountNumber')
  copyAccountNumber.setAttribute('type', 'text')
  copyAccountNumber.select()

  try {
    var successful = document.execCommand('copy')
    var msg = successful ? 'successful' : 'unsuccessful'
    console.log("copy was " + msg)
  } catch (err) {
    console.log("copy was " + err)
  }

  /* unselect the range */
  copyAccountNumber.setAttribute('type', 'hidden')
  window.getSelection().removeAllRanges()
}

export default {
  name: "AccountNumber",
  components: {

  },
  props: {
    showPopup: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
  methods: {
    hidePopup() {
      this.$emit("hidePopup")
    },
    clear() {
      this.target = ""
      this.bankData = {
        father: "",
        mother: "",
        my: ""
      }
      this.accountNumberData = {
        father: "",
        mother: "",
        my: ""
      }
    },
    showAlert(alertText) {
      this.alertText = alertText
      this.alertBar = true
    },
    setAccountNumberData(target) {
      if (target != null) {
        this.target = target
        this.name = this.accountNumbers[target].name
        this.bankData = this.accountNumbers[target].bankData
        this.accountNumberData = this.accountNumbers[target].accountNumberData
      } else {
        this.clear()
      }
    },
    copyClipboard(target) {
      //var text = this.bankData[target] + " " + this.accountNumberData[target] // 은행명 + 계좌번호
      var text = this.accountNumberData[target] // 계좌번호
      text = text.replace(/-/gi, '')
      this.copyAccountNumber = text

      copyAccountNumber(text)
      this.showAlert('계좌번호를 복사했습니다!')
      setTimeout(() => {
        this.copyAccountNumber = ""
      }, 3000)
    },
  },
  data: () => ({
    copyAccountNumber: "",
    alertBar: false,
    alertText: '',
    alertTimeout: 2000,
    target: "groom",
    name: {
      father: "",
      mother: "",
      my: ""
    },
    bankData: {
      father: "",
      mother: "",
      my: ""
    },
    accountNumberData: {
      father: "",
      mother: "",
      my: ""
    },
    accountNumbers: {
      groom: {
        name: {
          father: "",
          mother: "오영임",
          my: "이동찬"
        },
        bankData: {
          father: "",
          mother: "우리은행",
          my: "신한은행"
        },
        accountNumberData: {
          father: "",
          mother: "1002-851-616472",
          my: "110-518-608680"
        }
      },
      bride: {
        name: {
          father: "고문석 · 필립 보",
          mother: "안신자 · 프란체스카",
          my: "고혜영 · 마리아 막달레나"
        },
        bankData: {
          father: "국민은행",
          mother: "하나은행",
          my: "신한은행"
        },
        accountNumberData: {
          father: "846210115764",
          mother: "203-910065-82107",
          my: "110-468-172942"
        }
      },
    }
  }),
};
</script>

