<template>
  <div class="pa-0 ma-0">
    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col cols="12" class="pa-0 ma-0">
        <p class="p-title">라온제나 강남 8층 플로렌스홀</p>
        <p class="p-subtitle">2022년 11월 26일 토요일</p>
        <p class="p-subtitle">오후 12시 30분 예식이 시작됩니다.</p>
      </v-col>
      <v-col class="pa-0 mt-3 mb-3" cols="12">
        <v-btn
          fab
          small
          color="white"
          class="mr-5"
          @click="openNaverMapApp()"
        >
          <v-avatar
            size="40"
            class=""
            style="cursor: pointer;"
          >
            <img
              :src="require('@/assets/image/button/navermap.png')"
            >
          </v-avatar>
        </v-btn>
        <v-btn
          fab
          small
          color="white"
          class="mr-5"
          @click="openKakaoMapApp()"
        >
          <v-avatar
            size="40"
            class=""
            style="cursor: pointer;"
          >
            <img
              :src="require('@/assets/image/button/kakaomap.png')"
            >
          </v-avatar>
        </v-btn>
        <v-btn
          fab
          small
          color="white"
          @click="openTmapApp()"
        >
          <v-avatar
            size="40"
            class=""
            style="cursor: pointer;"
          >
            <img
              :src="require('@/assets/image/button/tmap.png')"
            >
          </v-avatar>
        </v-btn>
      </v-col>

      <v-col class="mb-3" cols="12">
        <v-btn @click="sendURLtoSMS()" class="" height="40" rounded color="white">
          <p class="p-subtitle2 mb-n1">청첩장 전달하기</p>
        </v-btn>
      </v-col>
    </v-row>
    
    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col cols="12">
        <v-img
          :src="require('@/assets/image/dday.png')"
          contain
          class="ma-0"
        >
        </v-img>
        <!-- <v-sheet height="400" width="600" style="display: inline-block;">
          <v-calendar
            ref="calendar"
            v-model="weddingDay"
            :now="weddingDay"
            :value="weddingDay"
            color="#00AAFF"
            :events="events"
            :event-color="getEventColor"
            :type="'month'"
            @click:event="showEvent"
            @click:more="viewDay"
            @click:date="viewDay"
            @change="updateRange"
          ></v-calendar>
        </v-sheet> -->
      </v-col>
    </v-row>
    <v-row class="pa-0 ma-0">
      <v-col cols="12" class="pa-0 mb-4">
        <v-img
          :src="require('@/assets/image/divider-small.png')"
          contain
          height="14"
          class="mt-3"
        >
        </v-img>
      </v-col>
      <v-col cols="12">
        <p class="p-title">결혼식까지 남은 시간</p>
        <p style="font-size: 36px;">{{dday}}</p>
      </v-col>
    </v-row>

    <input type="hidden" id="copyTextDDay" :value="copyTextDDay">

    <v-snackbar
      v-model="alertBar"
      :timeout="alertTimeout"
      color="rgba(255, 247, 132, 0.9)"
      centered
      height="50"
    >
      <p class="p-subtitle2 ma-0 mb-n1 ml-2" style="color: black;">{{ alertText }}</p>
      <template v-slot:action="{ attrs }">
        <v-btn
          icon
          small
          v-bind="attrs"
          @click="alertBar = false"
        >
          <v-icon
            color="red"
            small
          >
            mdi-window-close
          </v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
var ddayInterval = null

async function copyTextDDay(text) {
  try {
    await navigator.clipboard.writeText(text)
  } catch (err) {
    copyTextDDay2()
  }
}

function copyTextDDay2() {
  let copyText2 = document.querySelector('#copyTextDDay')
  copyText2.setAttribute('type', 'text')
  copyText2.select()

  try {
    var successful = document.execCommand('copy')
    var msg = successful ? 'successful' : 'unsuccessful'
    console.log("copy was " + msg)
  } catch (err) {
    console.log("copy was " + err)
  }

  /* unselect the range */
  copyText2.setAttribute('type', 'hidden')
  window.getSelection().removeAllRanges()
}

export default {
  name: 'DDay',
  created() {},
  mounted() {
    this.getDDay()
  },
  beforeDestroy() {
    clearInterval(ddayInterval)
    ddayInterval = null
  },
  unmounted() {
    clearInterval(ddayInterval)
    ddayInterval = null
  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  methods: {
    getDDay() {
      var self = this
      var weddingDay = new Date(this.weddingDay)
      // var def = weddingDay.getTime() - today.getTime()
      // var dday = Math.ceil(def / (1000 * 60 * 60 * 24))

      ddayInterval = setInterval(function () {
        var now = new Date()
        var gap = weddingDay.getTime() - now.getTime()
        var day = Math.ceil(gap / (1000 * 60 * 60 * 24)) - 1
        if (day < 10) {
          day = "0" + day
        }
        var hour = Math.ceil((gap % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) - 1
        if (hour < 10) {
          hour = "0" + hour
        }
        var min = Math.ceil((gap % (1000 * 60 * 60)) / (1000 * 60)) - 1
        if (min < 10) {
          min = "0" + min
        }
        var sec = Math.ceil((gap % (1000 * 60)) / (1000)) - 1
        if (sec < 10) {
          sec = "0" + sec
        }

        self.setDDay(day + "일 " + hour + ":" + min + ":" + sec)
      }, 1000)
    },
    setDDay(dday) {
      this.dday = dday
    },
    openTmapApp() {
      console.log("openTmapApp")
      window.location.href = "https://apis.openapi.sk.com/tmap/app/routes?appKey=l7xxf3df8e921a6e43beb8fbcb43854b1c20&name=라온제나 강남&lon=127.038589254902&lat=37.5169632798641"
    },
    openNaverMapApp() {
      console.log("openNaverMapApp")
      if (this.browserType == "android") {
        window.location.href = "intent://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish#Intent;scheme=nmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.nhn.android.nmap;end"
      } else if (this.browserType == "iphone") {
        var clickedAt = +new Date()
        window.location.href = "nmap://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish"

        setTimeout(function() {
          if (+new Date() - clickedAt < 2000) {
            location.href = 'http://itunes.apple.com/app/id311867728?mt=8'
          }
        }, 1500)
      } else {
        window.location.href = "https://naver.me/FXZrWN73"
      }
    },
    openKakaoMapApp() {
      console.log("openKakaoMapApp")
      try {
        window.location.href = "kakaomap://search?q=라온제나 강남&p=37.5169632798641,127.038589254902"
      } catch (err) {
        window.location.href = "https://map.kakao.com/link/to/라온제나 강남,37.5169632798641,127.038589254902"
      }
    },
    sendURLtoSMS() {
      this.copyClipboard("https://ldc0728.github.io/invitation-publish/")

      setTimeout(() => {
        if (this.browserType == "android") {
          window.location.href = "sms:?body=https://ldc0728.github.io/invitation-publish/"
        } else if (this.browserType == "iphone") {
          window.location.href = "sms:&body=https://ldc0728.github.io/invitation-publish/"
        }
      }, 1500)
    },
    copyClipboard(text) {
      this.copyTextDDay = text

      copyTextDDay(text)
      this.showAlert('청첩장 공유 URL을 복사했습니다!')
      setTimeout(() => {
        this.copyTextDDay = ""
      }, 500)
    },
    showAlert(alertText) {
      this.alertText = alertText
      this.alertBar = true
    },
  },
  data: () => ({
    weddingDay: '2022-11-26T12:30:00',
    dday: "",
    copyTextDDay: "",
    alertText: "",
    alertBar: false,
    alertTimeout: 1000,
  }),
}
</script>

